<template>
    <div class="wizard-adjuntos">
      <div class="container">
        <div class="card card-muni card-muni-medi">
          <h3>PROCESO DE PAGO</h3>
            <div class="product">
              <h5>{{dataPago.nombreTipoTramite}}</h5>
              <div class="row mt-4 mb-4">
                <div class="col-md-4 text-center">
                  <div class="view">
                    <p>Nmero de pedido</p>
                    <b>{{dataPago.numeroPedido}}</b>
                  </div>
                </div>
                <div class="col-md-4 text-center">
                  <div class="import">
                    <p>Importe {{descripcionImporte}}</p>
                    <b>S/. {{dataPago.precioTramite}}</b>
                  </div>
                </div>
                <div class="col-md-4 text-center">
                  <div class="view">
                    <p>Fecha</p>
                    <b>{{fechaActualx}}</b>
                  </div>
                </div>
                <div class="col-md-12 text-center mt-2">
                    <div v-if="pagoOk">
                        <div class="alert alert-success mt-3" role="alert">
                            El pago se realizo correctamente y se envi el detalle al correo de su usuario de sesin.
                        </div>
                        <!-- <div class="alert alert-success mt-3" role="alert">
                            El pago se realizo correctamente.
                        </div> -->
                    </div>
                    <div v-if="pagoError">
                        <div class="alert alert-danger mt-3" role="alert">
                            {{this.dataPago.mensaje}}
                        </div>
                    </div>
                </div>
                <hr/>                
                <div class="col-md-12 text-center mt-2" v-if="pagoError">
                    <!-- <div class="row"> -->
                        <button 
                            class="btn btn-danger" 
                            @click="reintentarPago()">
                            Intentarlo nuevamente
                        </button>
                    <!-- </div> -->
                </div>
                <div class="col-md-12 text-center mt-2" v-if="pagoOk">
                      <button class="btn btn-danger" @click.prevent="imprimirRecibo()">
                          Descargar recibo
                      </button>
                </div>
              </div>            
            </div>
          </div>
      </div>
    </div>
</template>
<script>
import EventBus from '../../helpers/EventBus'
import Util from '../../tools/util'
import Constantes from '../../store/constantes'
import _ from "lodash"

import {infoPago} from '../services/pagosService'

export default {
    data(){
        return {
            dataPago:{},
            descripcionImporte: "a pagar",
            mensajeResultado: "",
            pagoOk: false,
            pagoError: false,
            idArchivo: null
        }
    },
    mounted(){
        EventBus.$emit("loadershow", true);
        this.dataPago.numeroPedido = this.$route.params.numeroPedido;
        this.dataPago.resultado = this.$route.params.resultado;
        this.dataPago.mensaje = this.$route.params.mensaje;

        infoPago(this.dataPago.numeroPedido).then((response)=>{
          if(response.data.success){
            let data = response.data.data;

            this.dataPago.precioTramite = data.desMonto;
            this.dataPago.nombreTipoTramite = data.desPagoDesc;

            if(this.dataPago.resultado==="ok"){
                this.pagoOk = true;
                this.pagoError = false;
                this.descripcionImporte = "Pagado";

                let appRentas = Util.storage.get("appRentas");
                delete appRentas.cuenta;

                Util.storage.set("appRentas", appRentas);
                this.idArchivo = data.desNroord;
                console.log(response);
            }else{
                this.pagoOk = false;
                this.pagoError = true;
            }

            EventBus.$emit("loadershow", false);
          }
        }).catch(e => console.log(e));
    },
    methods:{
      reintentarPago(){
        let url = `${Constantes.urlIniciaPagoTributo}/contribuyente/estado-cuenta/seleccionados`;
        window.location.assign(url);
      },
      imprimirRecibo(){
            let documento = `${Constantes.filesRecupera}12/${this.idArchivo}/0`;
            window.open(documento, "_blank");
      }
    },
    computed: {
        fechaActualx() {
            let fc = this.fecTramite;
            if (fc == null) {
                fc = new Date();
            }
            return (
                fc.getUTCDate() +
                "/" +
                (fc.getUTCMonth() + 1) +
                "/" +
                fc.getUTCFullYear()
            );
        }
    }
}
</script>
<style lang="scss">

</style>


